

.help-container {
    display: flex;
    align-items: center;
    width: 60%;
    margin-left: 40%;
    margin-top: 7%;
}



.helptitle strong {
    margin: 0; /* Remove any unwanted extra margin */
    font-size: 20px;
    color: rgb(54, 53, 53);
    
    font-size: 1.9rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

   
}
.helpparagraph{
    width: 50%;
    color: rgb(173, 169, 169);
    margin-left: 25%;
    margin-top: 4%;
    text-align: justify;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.cardflexbox{
    margin-left: 21%;
    margin-top: 7%;
    flex-wrap: wrap;
    display: flex;
    gap: 40px;
}


.helpcard {
    width: 400px;
    height: 450px;
    color: rgb(173, 169, 169);
    align-items: center;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border-radius: 24px;
    border : 1px solid rgba(255, 255, 255, 0.05); 
}

.helpcard .no {
    font-size: 80px;
    color: #a5a1a159;
    margin-left: 7%;
    padding-top: 7%;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.cardtitle{
    margin-left: 7%;
    margin-top: 35%;
    font-weight: 700;
    color: rgb(71, 69, 69);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.cardcontent {
    margin-left: 7%;
    margin-top: 5%;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

   
}

.cardcontent p{
    text-align: justify;
    color: rgb(44, 43, 43);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.cardmaintitle{
    margin-left: 7%;
    margin-top: 10%;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.cardmaintitle span {
    color: rgb(61, 60, 60);
    font-size: 40px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

@media only screen and (max-width: 416px)and (min-width:385px)  {
    .cardflexbox{
        margin-left: 16%;
        width: 80%;
        
    }  
    .helpparagraph{ 
        margin-left: 17%;
        width: 66%;
    }

    .helpcard{
        width: 300px;
        height: 400px;
    }

    .helptitle strong{
        margin-left: -37%;
        font-size: 1.6rem;
    }
  }

  @media only screen and (max-width: 385px)  {
    .cardflexbox{
        margin-left: 14%;
    }  

    .helpcard{
        width: 90%;
        height:400px;
    }
    .helptitle strong{
        margin-left: -38%;
        font-size: 1.5rem;
    }
  }

  @media only screen and (max-width: 330px) and (min-width:320px){
    .cardflexbox{
        margin-left: 12%;
    }  

    .helpcard{
        width: 300px;
        height: 400px;
    }
    .helptitle{
        margin-left: -27%;
        font-size: 0.7rem;
    }
  }