    }

    .content p strong{
        
        justify-content: justify;
        width: 100%;
    }
    .fullcontent p{
        width: 80%;
      
    }
    .button button {
        margin-left: 14.5%;
    }

    .user{
        width:200px;
        margin-left: 40%;
        margin-top: -10%;
        order: 2;

    }
    .user img{
        width: 270px;
    }

    .title{
        margin-left: 70%;
        order: 1;
    }

    .dot{
        display: none;
    }
    .button{
        width: 80%;
    }
  }
