.contactcontent p{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: rgb(173, 169, 169);
    font-size: 40px;
    line-height: 1.5;
    letter-spacing: .01rem;
    font-weight: 200;
    width: 67%;
    text-align: justify;
    margin-left: 16%;
   

}
.pic{
    width: 80px;
    margin-bottom: -3.3%;
    margin-right: 1.5%;
}

.contactcontent{
    margin-left: 7%;
    margin-top: 10%;
}

.contactcontent p strong{
    color: rgb(54, 53, 53);
}

.touch{
    
    margin-left: 0%;
    margin-top: 4%;
    
    margin-bottom: 7%;
    
}

.touch button{
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    color: white;
    font-size: 0.8rem;
    width: 200px;
    padding: 10px;
    border-radius: 24px;
    text-align: center;
    
    cursor: url('../Assets//pointer.png'),auto;
    border : 1px solid rgba(255, 255, 255, 0.05); 
    margin-left: 15%;
    
}

/* .touch button:hover{
    background-color: transparent;
    color: black;
    border: 2px solid black;
} */

@media only screen and (max-width: 416px)and (min-width:385px) {
    .contactcontent p{
        font-size: 25px;
        width: 80%;
    }

    .touch {
        width: 80%;
    }
    .contactcontent{
        margin-left: 2%;
        margin-right: 5%;
    }

    .pic{
        width: 30px;
    }
    
  }

  
  @media only screen and (max-width: 385px) {
    .contactcontent p{
        font-size: 20px;
        width: 81%;
    }

    .contactcontent p{
        margin-left: 12%;
    }
    .touch {
        width: 80%;
    }

    .contactcontent{
        margin-left: 2%;
        margin-right: 5%;
    }

    .pic{
        width: 10%;
    }
  }

  @media only screen and (max-width: 330px) and (min-width:320px){

    .contactcontent p{
        font-size: 15px;
    }
    .touch {
        width: 80%;

    }

    
  }